import React from "react";
import { Progress } from "antd";

const Analytics = ({ allTransection }) => {
  // category
  let totalResult = 0;
  // total transaction
  const totalTransaction = allTransection.length;
  const totalIncomeTransactions = allTransection.filter(
    (transaction) => transaction.quantity
  );

  const totalIncome = () => {
    let totalIncome = 0;
    allTransection.forEach((transaction) => {
      totalIncome = totalIncome + transaction.quantity;
    });

    return totalIncome;
  };

  const totalIncomee = () => {
    let totalIncomee = 0;
    allTransection.forEach((transaction) => {
      totalIncomee = totalIncomee + transaction.amount;
    });

    return totalIncomee;
  };

  return (
    <>
      <div className="row m-3">
        <div className="col-md-3">
          <div className="card">
            <div className="card-header">
              Total Products : {totalTransaction}
            </div>
            <div className="card-body">
              <h5 className="text-success">Items : {totalTransaction}</h5>
              <h5 className="text-success">Quantity : {totalIncome()}</h5>
              <h5 className="text-success">Amount : {totalIncomee()}</h5>
            </div>
          </div>
        </div>
      </div>
      <div className="row mt-3 analytics"></div>
    </>
  );
};

export default Analytics;
