import React, { useState, useEffect } from "react";
import { Form, Input, message } from "antd";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import Spinner from "../components/layouts/Spinner";
import "../style/login.css";

const Login = () => {
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  //Form Submit
  const submitHandler = async (values) => {
    try {
      setLoading(true);
      const { data } = await axios.post("/api/v1/users/login", values);
      setLoading(false);
      message.success("/login success");
      localStorage.setItem(
        "user",
        JSON.stringify({ ...data.user, password: "" })
      );
      navigate("/");
    } catch (error) {
      setLoading(false);
      message.error("something went wrong");
    }
  };

  //prevent
  useEffect(() => {
    if (localStorage.getItem("user")) {
      navigate("/");
    }
  }, [navigate]);
  return (
    <>
      <div className="contain-1">
        <button className="btn-1">
          <i data-feather="arrow-left">Store Management System</i>
        </button>
        <div className="login">
          {loading && <Spinner />}
          <Form layout="vertical" onFinish={submitHandler} className="form-1">
            <h1>Welcome Back!</h1>
            <small>
              Login to your account and start connecting with the world. Your
              journey begins here.
            </small>
            <Form.Item label="Email" name="email" className="label">
              <Input type="email" className="input" />
            </Form.Item>
            <Form.Item label="Password" name="password" className="label">
              <Input type="password" className="input" />
            </Form.Item>
            <button className="log">Login</button>
          </Form>
        </div>
        <div className="image">
          <h1>Unlock the Path to Retail Excellence</h1>
        </div>
      </div>
    </>
  );
};

export default Login;
