import React, { useState, useEffect } from "react";
import { Form, Modal, Input, Select, message, Table } from "antd";
import {
  EditOutlined,
  DeleteOutlined,
  UnorderedListOutlined,
  AreaChartOutlined,
  DownloadOutlined,
} from "@ant-design/icons";
import Layout from "./../components/layouts/Layout";
import axios from "axios";
import Spinner from "../components/layouts/Spinner";
import Analytics from "../components/Analytics";

const HomePage = () => {
  const [showModal, setShowModal] = useState(false);
  const [loading, setLoading] = useState(false);
  const [allTransection, setAllTransection] = useState([]);
  const [editable, setEditable] = useState(null);
  const [viewData, setViewData] = useState("table");
  // Table Data
  const columns = [
    {
      title: "Date",
      dataIndex: "date",
    },
    {
      title: "Quantity",
      dataIndex: "quantity",
    },
    {
      title: "Items",
      dataIndex: "items",
    },
    {
      title: "TP",
      dataIndex: "tp",
    },
    {
      title: "Discount",
      dataIndex: "discount",
    },
    {
      title: "Amount",
      dataIndex: "amount",
    },
    {
      title: "Actions",
      render: (text, record) => (
        <div>
          <EditOutlined
            onClick={() => {
              setEditable(record);
              setShowModal(true);
            }}
          />
          <DeleteOutlined
            className="mx-2"
            onClick={() => {
              handleDelete(record);
            }}
          />
        </div>
      ),
    },
  ];

  // get all transection

  useEffect(() => {
    const getAllTransections = async () => {
      try {
        const user = JSON.parse(localStorage.getItem("user"));
        setLoading(true);
        const res = await axios.post("/api/v1/transections/get-transection", {
          userid: user._id,
        });
        setLoading(false);
        setAllTransection(res.data);
        console.log(res.data);
      } catch (error) {
        console.log(error);
        message.error("issue with product");
      }
    };
    getAllTransections();
  }, [setAllTransection]);

  const handleDelete = async (record) => {
    try {
      setLoading(true);
      await axios.post("/api/v1/transections/delete-transection", {
        transectionId: record._id,
      });
      setLoading(false);
      message.success("Product deleted");
    } catch (error) {
      setLoading(false);
      console.log(error);
      message.error("unable to delete");
    }
  };

  // Form handle
  const handleSubmit = async (values) => {
    try {
      const user = JSON.parse(localStorage.getItem("user"));
      setLoading(true);
      if (editable) {
        await axios.post("/api/v1/transections/edit-transection", {
          payload: {
            ...values,
            userId: user._id,
          },
          transectionId: editable._id,
        });
        setLoading(false);
        message.success("Product Updated Successfully");
      } else {
        await axios.post("/api/v1/transections/add-transection", {
          ...values,
          userid: user._id,
        });
        setLoading(false);
        message.success("Product Added Successfully");
      }
      setShowModal(false);
      setEditable(null);
    } catch (error) {
      setLoading(false);
      message.error("Failed to add Product");
    }
  };

  return (
    <Layout>
      {loading && <Spinner />}
      <div className="filters">
        <div className="switch-icons">
          <UnorderedListOutlined
            className={`mx-2 ${
              viewData === "table" ? "active-icon" : "inactive-icon"
            }`}
            onClick={() => setViewData("table")}
          />
          <AreaChartOutlined
            className={`mx-2 ${
              viewData === "analytics" ? "active-icon" : "inactive-icon"
            }`}
            onClick={() => setViewData("analytics")}
          />
          <DownloadOutlined
            className={`mx-2 ${
              viewData === "analytics" ? "active-icon" : "inactive-icon"
            }`}
          />
        </div>
        <div>
          <button
            className="btn btn-primary"
            onClick={() => setShowModal(true)}
          >
            Add New
          </button>
        </div>
      </div>
      <div className="content">
        {viewData === "table" ? (
          <Table columns={columns} dataSource={allTransection} />
        ) : (
          <Analytics allTransection={allTransection} />
        )}
      </div>
      <Modal
        title={editable ? "Edit Transection" : "Add Transection"}
        open={showModal}
        onCancel={() => setShowModal(false)}
        footer={false}
      >
        <Form
          layout="vertical"
          onFinish={handleSubmit}
          initialValues={editable}
        >
          <Form.Item label="Qty" name="quantity">
            <Input type="number" />
          </Form.Item>
          <Form.Item label="Items" name="items">
            <Input type="text" />
          </Form.Item>
          <Form.Item label="TP" name="tp">
            <Input type="number" />
          </Form.Item>
          <Form.Item label="Discount" name="discount">
            <Input type="Number" />
          </Form.Item>
          <Form.Item label="Amount" name="amount">
            <Input type="number" />
          </Form.Item>
          <Form.Item label="Date" name="date">
            <Input type="date" />
          </Form.Item>
          <div className="d-flex justify-content-end">
            <button type="submit" className="btn btn-primary">
              {" "}
              SAVE{" "}
            </button>
          </div>
        </Form>
      </Modal>
    </Layout>
  );
};

export default HomePage;
